import React from "react";
import {
    Dialog,
    DialogTitle,
    Box,
    IconButton,
    DialogContent,
    Typography,
    Tooltip,
} from "@mui/material";
import CloseIcon from "@material-ui/icons/Close";
import { Colors } from "../../styles/theme";
import { Product, ProductImage, ProductDetailWrapper, StyledLink } from "../../styles/products";
import { Link } from "react-router-dom";
import { useUIContext } from "../../context/ui";
import {LinkedInShareButton, TwitterShareButton} from "../sharesocial";
import { HoverIconButton } from "../../styles/appbar";


const renderText = (data) => {
    if (!data) return null;

    return data.map((item) => (
        <Typography key={item.id} {...item.props}>
            {item.content.map((content, index) => {
                switch(content.type) {
                    case 'text':
                        return <span key={index} style={content.style}>{content.value}</span>;
                    case 'link':
                        return (
                            <span key={index} style={content.style}>
                                <StyledLink href={content.link}   >
                                    {content.linkContent}
                                </StyledLink>
                            </span>
                        );
                    default:
                        return null;
                }
            })}
        </Typography>
    ));
}



export default function TextProduct({ productDetails }) {
    const { contentPage } = useUIContext();


    const articleDetails = {
        articleUrl: 'https://musja.com',
        title: productDetails.title || 'Fallback Title',
        summary: productDetails.summary || 'Fallback summary',
        source: productDetails.source || 'Fallback source'
    };


    return (
        <Dialog
            open={true}
            fullScreen
            sx={{ zIndex: 90 }}
        >

            <DialogTitle sx={{ background: Colors.secondary, textAlign: "center" }}>
                <Box display="flex" justifyContent={"center"} alignItems={"center"} spacing={3}>

                    {contentPage !== "about" && <LinkedInShareButton {...articleDetails} />}
                    {contentPage !== "about" && <TwitterShareButton {...articleDetails} /> }


                    <Tooltip title="Back to main page">
                        <Link to={`/${contentPage}`}>
                            <HoverIconButton 
                                aria-label="Back to main page" 
                                size="large" 
                                sx={{ marginLeft: "20px", fontSize: "1em" }}  
                            >
                                <CloseIcon fontSize="inherit" />
                            </HoverIconButton>
                        </Link>
                    </Tooltip>

                </Box>
            </DialogTitle>


            <DialogContent   >
                <ProductDetailWrapper display={"flex"} flexDirection="column" alignItems="center">
                    <div style={{maxWidth: "800px", alignContent: "center", textAlign: "center"  }}  >
                        {renderText(productDetails.header)}
                    </div>
                        

                    <Product sx={{ maxWidth: "500px" }}>
                        <ProductImage src={productDetails.image} />
                    </Product>

                    <div style={{maxWidth: "800px"}}  >
                        {renderText(productDetails.text) }
                    </div>
                        
 

                </ProductDetailWrapper>
            </DialogContent>
        </Dialog>
    );
}

