import React from "react";
import { ThemeProvider } from "@mui/material"
import theme from "./styles/theme";
import Appbar from "./components/appbar";
import Products from "./components/products";
import { UIProvider } from "./context/ui";
import { Routes, Route, HashRouter } from "react-router-dom";
import ProductDetail from "./components/productdetail"; 
import NoPage from "./components/productdetail/NoPage";
import { AppContainer } from "./styles/app";

function App() {
  return (
    <ThemeProvider theme={theme} >
      <HashRouter>
        <AppContainer disableGutters maxWidth={false}>
          <UIProvider>
            <Routes>
              <Route path="/articles" element={<><Appbar /><Products content="articles" /></>} />
              <Route path="/about" element={<><Appbar /><Products content="about" /></>} />
              <Route path="/" element={<><Appbar /><Products content="articles" /></>} />
              <Route path="/:productId/*" element={<><Appbar /><ProductDetail /></>} />
              <Route path="*" element={<NoPage/>} />
            </Routes>
          </UIProvider>
        </AppContainer>
      </HashRouter>
    </ThemeProvider>
  );
}

export default App;
