import React, { useEffect, useState } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import TextProduct from "./TextProduct";
import NoPage from "./NoPage";

export default function ProductDetail() {

    let { productId } = useParams();
    const pattern = "^(article|about)-\\d{4}-\\d{2}-\\d{2}-\\w+(-\\w+)*$";
    const regex = new RegExp(pattern);

    const [productDetails, setProductDetails] = useState({ header: [{ id: "product-title", content: [{ "type": "text", "value": "Product title" }] }] });
    const [error, setError] = useState(false);

    useEffect(() => {

        const fetchProductDetails = async () => {
            try {
                const response = await axios.get(`./products/${productId}.json`);

                if (response.data && response.data.image && response.data.text && response.data.header) {
                    setProductDetails(response.data);
                } else {
                    setError(true);
                }
            } catch (error) {
                console.error("Error fetching product details:", error);
                setError(true);
            }
        };

        fetchProductDetails();

    }, [productId]);

    if (!regex.test(productId) || error) {
        return <NoPage />;
    }
    return <TextProduct productDetails={productDetails} />;
}

