import React, { useState, useEffect } from 'react';
import { Container, Grid } from '@mui/material';
import SingleProduct from './SingleProduct';
import { useUIContext } from '../../context/ui';
import articlesData from '../../static/data/articles';
import aboutData from '../../static/data/about';

function getDataBasedOnContent(contentType) {
    if(contentType === "articles") {
        return articlesData;
    } else if(contentType === "about") {
        return aboutData;
    } else {
        return [];
    }
}

function Products({content}) {
    const { contentPage, setContentPage } = useUIContext();
    const [items, setItems] = useState([]);

    useEffect(() => {
        if (content !== contentPage) {
            setContentPage(content);
        } 
        setItems(getDataBasedOnContent(content));
    }, [content, contentPage, setContentPage]);

    return (
        <Container maxWidth={"100%"} sx={{marginTop: "80px", marginBottom: "40px" }}>
            <Grid 
                container
                maxWidth={"100%"}
                width={"100%"}
                spacing={{ xs: 2, md: 3 }}
                justifyContent="center"
                sx={{ margin: `20px 4px 10px 4px` }}
                columns={{ xs: 4, sm: 8, md:8, lg: 12, xl: 16 }}
            >
                {items.map((item) => (
                    <Grid 
                        item 
                        key={item.id} 
                        xs={4}
                        display="flex" 
                        flexDirection={'column'} 
                        alignItems="center"
                    >
                        <SingleProduct product={item} />
                    </Grid>
                ))}
            </Grid>
        </Container>
    );
}

export default Products;


