import React from 'react';
import { LinkedInButtonComponent, TwitterButtonComponent   } from '../../styles/sharesocial';

const openPopup = (url, width, height, name) => {
    const left = (window.innerWidth - width) / 2;
    const top = (window.innerHeight - height) / 2;
    window.open(url, name, `status=1,width=${width},height=${height},top=${top},left=${left}`);
};

export function LinkedInShareButton({ articleUrl, title, summary, source }) {
    const baseUrl = 'https://www.linkedin.com/shareArticle?mini=true';
    const shareUrl = `${baseUrl}&url=${encodeURIComponent(articleUrl)}&title=${encodeURIComponent(title)}&summary=${encodeURIComponent(summary)}&source=${encodeURIComponent(source)}`;

    return <LinkedInButtonComponent openPopup={() => openPopup(shareUrl, 600, 400, 'LinkedInShare')} />;
}

export function TwitterShareButton({ articleUrl, title, via, hashtags }) {
    const baseUrl = 'https://twitter.com/intent/tweet';
    const shareUrl = `${baseUrl}?text=${encodeURIComponent(title)}&url=${encodeURIComponent(articleUrl)}${via ? `&via=${encodeURIComponent(via)}` : ''}${hashtags ? `&hashtags=${encodeURIComponent(hashtags)}` : ''}`;

    return <TwitterButtonComponent openPopup={() => openPopup(shareUrl, 600, 400, 'TwitterShare')} />;
}
