import React from 'react';
import Divider from '@mui/material/Divider';
import { 
    ActionIconsContainer,  
    MyList,
    MyListItemIcon,
} from '../../styles/appbar';
import { useUIContext } from '../../context/ui';
import { ActionStyledLink } from '../../styles/appbar';

export default function Actions() {
    const { contentPage } = useUIContext();
    return (
        <ActionIconsContainer>
            <MyList type="row">
                <ActionStyledLink to="/articles" active={contentPage === "articles"}>
                    <MyListItemIcon>Articles</MyListItemIcon>
                </ActionStyledLink>      
                <Divider orientation="vertical" flexItem />      
                <ActionStyledLink to="/about" active={contentPage === "about"}>
                    <MyListItemIcon>About</MyListItemIcon>
                </ActionStyledLink>  
                <Divider orientation="vertical" flexItem /> 
            </MyList>   
        </ActionIconsContainer>
    )
}
