import React from "react";
import { styled } from "@mui/material/styles";
import { Colors } from "../theme";

// LinkedIn Styled Button
const LinkedInShareButton = styled('button')(() => ({
    display: 'inline-block',
    backgroundColor: Colors.linkedinButton,
    color: Colors.white,
    padding: '10px 15px',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
    fontSize: '1rem',
    transition: 'background-color 0.2s',
    marginRight: '1em',

    '&:hover': {
        backgroundColor: Colors.linkedinButtonHover,
    },

    '&:focus': {
        outline: 'none',
    },
}));

export const LinkedInButtonComponent = ({ openPopup, shareUrl }) => {
    return (
        <LinkedInShareButton onClick={(e) => {
            e.preventDefault();
            openPopup(shareUrl, 600, 400);
        }}>
            Share on LinkedIn
        </LinkedInShareButton>
    );
};

// Twitter Styled Button
const TwitterShareButton = styled('button')(() => ({
    fontFamily: 'Arial, sans-serif',
    fontSize: '16px',
    padding: '10px 20px',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
    transition: 'background-color 0.3s ease',
    color: Colors.white,
    textDecoration: 'none',
    outline: 'none',
    boxShadow: '0px 2px 4px rgba(0, 0, 0, 0.1)',
    backgroundColor: Colors.twitterButton,

    '&:hover': {
        backgroundColor: Colors.twitterButtonHover,
    },
}));

export const TwitterButtonComponent = ({ openPopup, shareUrl }) => {
    return (
        <TwitterShareButton onClick={(e) => {
            e.preventDefault();
            openPopup(shareUrl, 600, 400);
        }}>
            Share on Twitter
        </TwitterShareButton>
    );
};
